.footer {
  display: none;
}

.footer__social {
  margin-top: 12px;
  max-width: 300px;
}

.footer__copyright {
  margin-top: 12px;
}

.footer--shown {
  display: block;
  padding-bottom: 30px;
}

.footer__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer--shown .footer__social {
  margin-bottom: 20px;
}

@media (min-width: $tablet) {
  .footer {
    display: block;
    padding: 33px 0 51px;
  }

  .footer__flex {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: $desktop) {
  .footer {
    padding: 60px 0 87px;
  }
}
